import React, { FC } from 'react';
import { CommonProps } from '@interface';
import './index.less';
import { scoreCeil } from '@util';
import { ItemClickType } from '@enum';

export enum StarValue {
    // 整颗星展示正面
    Front = 0,
    // 整颗星展示反面
    Reverse = 1,
    // 半星正面 半星反面
    Half = 2,
}

const Star: FC<CommonProps> = (props = {}) => {
    const {
        schema: {
            className = '',
            count = 5,
            isShowAppScoreVal = false,
            frontColor = '#FE3666',
            reverseColor = 'rgba(254, 54, 103, 0.3)',
            size = 'size-12',
            logInfo: { itemClickType = ItemClickType.PLAYEND_APP_SCORE_CLICK } = {},
        },
        style = {},
        info: { appScore = 0, isDownload },
    } = props;

    const value = scoreCeil(Number(appScore) / 10);

    const starList = new Array(count).fill(0);

    if (!isDownload) {
        return;
    }

    if (className === 'yellStar') {
        return (
            <div className={className} data-itemClickType={itemClickType} style={style}>
                {new Array(5).fill(0).map((k, i) => (
                    <div className={`yellStarItem${i}`} />
                ))}
            </div>
        );
    }
    if (className === 'smileStar') {
        return (
            <div className={className} data-itemClickType={itemClickType} style={style}>
                {new Array(5).fill(0).map((k, i) => (
                    <div className={`smileStarItem${i}`} />
                ))}
            </div>
        );
    }
    return (
        <>
            {isDownload && (
                <div
                    className={`star-group ${className} ${size}`}
                    data-itemClickType={itemClickType}
                    style={style}
                >
                    {starList.map((_, i) => {
                        const val =
                            i + 1 <= value
                                ? StarValue.Front
                                : i + 0.5 <= value && Math.floor(value) === i
                                    ? StarValue.Half
                                    : StarValue.Reverse;
                        let [left, right] = ['', ''];

                        switch (val) {
                        case StarValue.Front:
                            left = frontColor;
                            right = frontColor;
                            break;
                        case StarValue.Reverse:
                            left = reverseColor;
                            right = reverseColor;
                            break;
                        case StarValue.Half:
                            left = frontColor;
                            right = reverseColor;
                            break;
                        default:
                            break;
                        }

                        return (
                            <div className="star">
                                <div style={{ position: 'relative' }}>
                                    <svg
                                        className="starLeft"
                                        style={{ position: 'absolute', top: 0, left: 0 }}
                                        height="100%"
                                        width="100%"
                                        viewBox="0 0 24 24"
                                        version="1.1"
                                        xmlns="http://www.w3.org/2000/svg"
                                    >
                                        <g fill={right}>
                                            <g>
                                                <path
                                                    id="星形复制"
                                                    d="M10.48 20.69L5.17 22.95C4.88 23.08 4.53 22.94 4.41 22.64C4.37 22.55 4.35 22.46 4.36 22.36L4.86 16.59C4.96 15.53 4.62 14.48 3.92 13.68L0.14 9.31C-0.07 9.06 -0.04 8.69 0.2 8.48C0.27 8.42 0.36 8.37 0.45 8.35L6.07 7.05C7.1 6.81 7.99 6.16 8.53 5.25L11.5 0.28C11.67 0.01 12.02 -0.08 12.3 0.08C12.38 0.13 12.45 0.2 12.5 0.28L15.47 5.25C16.01 6.16 16.9 6.81 17.93 7.05L23.55 8.35C23.86 8.42 24.06 8.74 23.98 9.06C23.96 9.15 23.92 9.24 23.86 9.31L20.08 13.68C19.38 14.48 19.04 15.53 19.14 16.59L19.64 22.36C19.67 22.68 19.43 22.97 19.11 23C19.01 23.01 18.92 22.99 18.83 22.95L13.52 20.69C12.55 20.28 11.45 20.28 10.48 20.69Z"
                                                />
                                            </g>
                                        </g>
                                    </svg>
                                    {left === right ? null : (
                                        <svg
                                            className="starRight"
                                            style={{ position: 'absolute', top: 0, left: 0 }}
                                            height="100%"
                                            width="100%"
                                            viewBox="0 0 24 24"
                                            version="1.1"
                                        >
                                            <g
                                                stroke="none"
                                                strokeWidth="1"
                                                fill="none"
                                                fillRule="evenodd"
                                            >
                                                <g fill={left}>
                                                    <g>
                                                        <path
                                                            d="M10.4797914,20.6901299 L5.17244092,22.9532432 C4.87559307,23.0798224 4.53286131,22.9405579 4.4069281,22.6421873 C4.36962933,22.5538162 4.35446758,22.4575976 4.36277202,22.3619666 L4.86407427,16.5891424 C4.9558392,15.532409 4.61677261,14.4835162 3.92457097,13.6828156 L0.143145752,9.30867245 C-0.0683548753,9.06402024 -0.0424924207,8.69335576 0.200911161,8.48077016 C0.273002191,8.41780678 0.359359231,8.37357995 0.452411754,8.35196684 L6.06958403,7.0472786 C7.09782488,6.80845136 7.98551275,6.1601999 8.5292356,5.25106985 L11.4995367,0.284587401 C11.6055346,0.107354062 11.8086172,0.00632047488 12,0 C12,1.07559041 12,18.5361306 12,20.3794274 C11.4827473,20.3794274 10.9655549,20.4829949 10.4797914,20.6901299 Z"
                                                            id="end-page_icon_light_half复制"
                                                        />
                                                    </g>
                                                </g>
                                            </g>
                                        </svg>
                                    )}
                                </div>
                            </div>
                        );
                    })}
                    {isShowAppScoreVal ? <div className="appScoreVal">{value}</div> : null}
                </div>
            )}
        </>
    );
};

export default Star;
